<script>
  export default{
    data(){
      return{
         tags:[0,1]  //
      }
    },
    methods:{
      addTag(){
        if(this.tags.length){
          this.tags.push(this.tags[this.tags.length-1]+1);
        }else{
          this.tags.push(0);
        }
      },
      closeTag(event,name){
        const index=this.tags.indexOf(name);
        this.tags.splice(index,1);
      }
    }
  }
</script>


